import React, { useState, useCallback, useEffect, useRef, ReactNode } from 'react';
import { Icon } from '../../components/icons/icon';
import { ControlMenu } from './ControlMenu';

const containedInControlMenu = (target: any): boolean => {
    let current = target;
    for (let i = 0; current && i < 100; i++) {
        if (current && current.className === 'game-menu') return true;
        current = current.parentElement;
    }
    return false;
};

type ControlBarProps = { undo: () => void; restart: () => void; children: ReactNode };

export const ControlBar = ({ undo, restart, children }: ControlBarProps) => {
    const [open, setOpen] = useState<boolean>(false);
    const openRef = useRef<boolean>(open);
    openRef.current = open;

    const toggleMenu = useCallback(() => setOpen((s) => !s), []);

    const close = useCallback((event: any) => {
        if (openRef.current && event && !containedInControlMenu(event.target)) setOpen(false);
    }, []);

    useEffect(() => {
        window.addEventListener('click', close, true);
        return () => window.removeEventListener('click', close, true);
    }, [close]);

    return (
        <div className="game">
            <nav>
                <button onClick={toggleMenu} className="menu">
                    <Icon.Menu />
                </button>
            </nav>
            {open && <ControlMenu restart={restart} undo={undo} />}
            {children}
        </div>
    );
};
