import React, { useCallback, useState } from 'react';
import { Util } from '../../util/util';
import { DecryptoStore, useDecryptoSub } from './Decrypto-store';
import { DecryptoMe } from './DecryptoMe';
import { QrCodeOverlay } from './QrCodeOverlay';
import { DecryptoEnemy } from './DecryptoEnemy';
import { Icon } from '../../components/icons/icon';
import { NotifyResult } from './NotifyResult';
import { __ } from 'first-intl';

export const DecryptoInternal: React.FC = () => {
    const [showEnemy, setShowEnemy] = useState<boolean>(false);
    const { losses, wins } = useDecryptoSub(({ results }) => results);

    const onSwitch = useCallback(() => setShowEnemy((s) => !s), []);

    return (
        <div
            className={Util.classNames('decrypto', showEnemy && 'enemy', losses > 0 && 'alarm', wins > 0 && 'success')}>
            <NotifyResult />
            <div className="navigation">
                <button type="button" onClick={onSwitch}>
                    <Icon.Exchange />
                </button>
                <button className="restart" type="button" onClick={DecryptoStore.reset}>
                    {__('APP.restart')}
                </button>
            </div>
            <div className="content">{showEnemy ? <DecryptoEnemy /> : <DecryptoMe />}</div>
            <QrCodeOverlay />
        </div>
    );
};
