import React from 'react';
import { Cube } from './Cube';
import { Color } from './types';
import { Icon } from '../../components/icons/icon';
import { Box } from './Box';
import { allLetters } from './Letters';
import { TurnState } from './again-store';
import { __ } from 'first-intl';

export const Abort = () => (
    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 500 500">
        <g stroke="red" strokeWidth="100">
            <line x1="80" y1="80" x2="420" y2="420" />
            <line x1="80" y1="420" x2="420" y2="80" />
        </g>
    </svg>
);

export const Finish = () => (
    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 500 500">
        <g stroke="darkgreen" strokeWidth="100">
            <line x1="70" y1="180" x2="262" y2="347" />
            <line x1="220" y1="320" x2="420" y2="80" />
        </g>
    </svg>
);

export const Turn = ({ count = 0, color, skipped }: Partial<TurnState>) =>
    skipped ? (
        <div>
            <Icon.Ban className="cube" />
        </div>
    ) : (
        <div>
            <Cube count={count} />
            <Cube color={color} />
        </div>
    );

type TurnControlProps = { chosen?: { count: number; color: Color }; onFinish: () => void; onAbort: () => void };
export const TurnControl = ({ chosen, onFinish, onAbort }: TurnControlProps) => (
    <div className="again-turn">
        {!chosen && (
            <div className="skip" onClick={onFinish}>
                {__('AGAIN.skip')}
            </div>
        )}
        {chosen && (
            <div className="abort" onClick={onAbort}>
                <Abort />
            </div>
        )}
        <div className="main-control">{chosen ? <Turn count={chosen.count} color={chosen.color} /> : <Turn />}</div>
        {chosen && (
            <div className="finish" onClick={onFinish}>
                <Finish />
            </div>
        )}
    </div>
);

const Success: React.FC<{ turn: TurnState }> = ({ turn }) => {
    const { completedColor, completedRows } = turn;
    const completed = [];
    if (completedColor) completed.push(<Cube color={completedColor} key="color" />);
    if (completedRows)
        completedRows.forEach((isCompleted, index) => {
            if (isCompleted) completed.push(<Box content={allLetters[index]} key={index} />);
        });
    return completed.length > 0 ? (
        <>
            <Icon.ArrowRight className="success-arrow" />
            {completed}
        </>
    ) : null;
};

type TurnsProps = { turns: TurnState[] };
export const Turns = ({ turns }: TurnsProps) => (
    <div className="again-turns">
        <h4>{__('AGAIN.madeTurns')}</h4>
        {turns.map((turn, index) => (
            <div className="again-turn" key={index}>
                {`${index + 1}) `}
                <div className="turn-chosen">
                    <Turn count={turn.count} color={turn.color} skipped={turn.skipped} key={index} />
                </div>
                <div className="turn-success">
                    <Success turn={turn} />
                </div>
            </div>
        ))}
    </div>
);
