import React from 'react';
import { Icon } from '../../components/icons/icon';
import { __ } from 'first-intl';
import { HistoryStore } from './history-store';

export const NavBar: React.FC<{ started: boolean }> = ({ started }) => (
    <nav>
        <button onClick={HistoryStore.undo} className="undo">
            <Icon.Undo />
        </button>
        {started && (
            <button onClick={HistoryStore.reset} className="restart">
                {__('APP.restart')}
            </button>
        )}
        {started && (
            <button onClick={HistoryStore.rearrange} className="rearrange">
                <Icon.Refresh />
            </button>
        )}
    </nav>
);
