import { Persistore } from 'persistore';
import { Util } from '../../util/util';
import { AllData, Data, dataOptions, DataOption } from './data';
import { Color } from './types';
import { createStore } from 'react-use-sub';

export type SplashIconType = 'cancel' | 'undo' | 'submit' | 'skip';

export type TurnState = {
    skipped: boolean;
    color?: Color;
    count?: number;
    completedColor?: Color;
    completedRows?: boolean[];
};

export type VeryCleverState = {};

export type AgainState = {
    crossed: boolean[][];
    rowPointsBlocked: boolean[];
    colorPointsBlocked: boolean[];
    takenJokers: number;
    turns: TurnState[];
};

type State = {
    againSplash: SplashIconType | null;
    again: AgainState;
    againData: Data | null;
    veryClever: VeryCleverState;
};

const [useAgainSub, store] = createStore<State>({
    againSplash: null,
    again: { crossed: [], rowPointsBlocked: [], colorPointsBlocked: [], takenJokers: 0, turns: [] } as AgainState,
    againData: null,
    veryClever: {},
});

export { useAgainSub };

const setSplash = (icon: SplashIconType | null) => store.set({ againSplash: icon });

const _key = 'again';
const _keyChosen = 'again_chosen';

const getHistory = (): AgainState[] => {
    try {
        const states = Persistore.get(_key);
        return JSON.parse(states as any);
    } catch (e) {
        return [];
    }
};

const randomNum = (limit: number) => Math.floor(Math.random() * limit);
const randomDataKey = (): DataOption => dataOptions[randomNum(dataOptions.length)];

const dataOption = (option: DataOption | 'random'): DataOption => (option === 'random' ? randomDataKey() : option);

const chooseData = (option: DataOption): void => {
    const selectedOption = dataOption(option);
    store.set({ againData: AllData[selectedOption] });
    Persistore.set(_keyChosen, selectedOption);
};

const set = (state: Partial<AgainState>): void => {
    store.set(({ again }) => ({ again: { ...again, ...state } }));
    const history = getHistory();
    history.unshift(store.get().again);
    if (history.length > 10) history.pop();
    Persistore.set(_key, JSON.stringify(history));
};

const undo = (): void => {
    if (!store.get().againSplash) {
        const history = getHistory();
        if (history.length > 1) {
            history.shift();
            Persistore.set(_key, JSON.stringify(history));
            store.set({ again: history[0], againSplash: 'undo' });
        } else {
            Persistore.remove(_key);
            store.set({ again: initialData, againSplash: history.length > 0 ? 'undo' : null });
        }
    }
};

const uncrossedRow: boolean[] = Util.array(7).map(() => false);
export const uncrossedGrid: boolean[][] = Util.array(15).map(() => uncrossedRow);
const unblockedRowPoints: boolean[] = Util.array(15).map(() => false);
const unblockedColorPoints: boolean[] = Util.array(5).map(() => false);
const initialData: AgainState = {
    crossed: uncrossedGrid,
    rowPointsBlocked: unblockedRowPoints,
    colorPointsBlocked: unblockedColorPoints,
    takenJokers: 0,
    turns: [],
};

const get = (): AgainState => getHistory()[0] || initialData;
const getChosenData = (): Data | null => {
    const option = Persistore.get(_keyChosen);
    return option ? AllData[(option as any) as DataOption] : null;
};

const init = () => store.set({ again: get(), againData: getChosenData() });

const restart = (): void => Persistore.remove(_key) || Persistore.remove(_keyChosen) || init();

init();

export const AgainStore = { set, undo, restart, chooseData, setSplash };
