import React, { useState, useEffect, useCallback } from 'react';
import { AgainStore, TurnState, uncrossedGrid, useAgainSub } from './again-store';
import { ControlBar } from './ControlBar';
import { Data } from './data';
import { Jokers } from './Jokers';
import { Grid } from './Grid';
import { Letters } from './Letters';
import { calculate, getClickable, getCompletedColors, getCompletedRows } from './again-logic';
import { RowPoints } from './RowPoints';
import { ColorList, ColorPoints } from './ColorPoints';
import { Total } from './Total';
import { TurnControl, Turns } from './Turn';
import { Util } from '../../util/util';
import { Color } from './types';
import { CompletionPopup } from './CompletionPopup';
import { ActionSplash } from './ActionSplash';

type Chosen = { color: Color; count: number };
const calculateTurn = (
    chosen: Chosen | undefined,
    lastCrossed: boolean[][],
    crossed: boolean[][],
    gridData: Data
): TurnState => {
    if (chosen) {
        const completedRows = getCompletedRows(lastCrossed);
        const nextCompletedRows = getCompletedRows(crossed);
        const newCompletedRows = nextCompletedRows.map((row, index) => row && !completedRows[index]);

        const nextCompletedColors = getCompletedColors(crossed, gridData);
        const completedColors = getCompletedColors(lastCrossed, gridData);

        const newCompletedColor = nextCompletedColors
            .map((row, index) => row && !completedColors[index])
            .findIndex(Boolean);
        return {
            ...chosen,
            skipped: false,
            completedRows: newCompletedRows,
            completedColor: ColorList[newCompletedColor] as any,
        };
    }
    return { skipped: true };
};

type AgainStartedState = {
    crossed: boolean[][];
    turnCrossed: boolean[][];
    chosen?: Chosen;
    completedColor?: Color;
};

export const AgainStarted: React.FC<{ gridData: Data }> = ({ gridData }) => {
    const { again, againSplash } = useAgainSub(({ again, againSplash }) => ({ again, againSplash }));
    const [state, setState] = useState<AgainStartedState>({
        crossed: again.crossed,
        turnCrossed: uncrossedGrid,
    });
    const { crossed, chosen, turnCrossed, completedColor } = state;

    useEffect(() => {
        setState((s) => ({ ...s, crossed: again.crossed }));
    }, [again]);

    const onCross = useCallback(
        (boxIndex: number, rowIndex: number): void => {
            const currentRow = crossed[rowIndex];
            if (!currentRow[boxIndex]) {
                const color = gridData.grid[rowIndex][boxIndex];
                if (chosen) {
                    if (color !== chosen.color) return; // many colors is not allowed
                    const alreadyPicked = chosen.count;
                    if (alreadyPicked > 4) return; // no more than 5 per color and turn
                }
                const updatedRow = Util.nextArray(currentRow, boxIndex, true);
                const updatedTurnRow = Util.nextArray(turnCrossed[rowIndex], boxIndex, true);
                const count = chosen ? chosen.count + 1 : 1;
                setState((s) => ({
                    ...s,
                    crossed: Util.nextArray(crossed, rowIndex, updatedRow),
                    turnCrossed: Util.nextArray(turnCrossed, rowIndex, updatedTurnRow),
                    chosen: { color, count },
                }));
            }
        },
        [crossed, chosen, turnCrossed, gridData]
    );

    const onFinish = useCallback(() => {
        if (!againSplash) {
            const madeTurn = calculateTurn(chosen, again.crossed, crossed, gridData);

            AgainStore.set({ crossed, turns: [...again.turns, madeTurn] });
            AgainStore.setSplash(madeTurn.skipped ? 'skip' : 'submit');

            setState((s) => ({
                ...s,
                turnCrossed: uncrossedGrid,
                chosen: undefined,
                completedColor: madeTurn.completedColor,
            }));
        }
    }, [againSplash, chosen, again.crossed, again.turns, crossed, gridData]);

    const onAbort = useCallback(() => {
        if (!againSplash) {
            AgainStore.setSplash('cancel');
            setState({ turnCrossed: uncrossedGrid, chosen: undefined, crossed: again.crossed });
        }
    }, [againSplash, again]);

    const onClosePopup = useCallback(() => setState((s) => ({ ...s, completedColor: undefined })), []);

    const { rowPointsBlocked, colorPointsBlocked, takenJokers, turns } = again;
    const { completedRows, completedColors, totalPoints } = calculate(again, gridData);
    const { backgroundColor } = gridData;
    const clickable = chosen ? getClickable(turnCrossed, false) : getClickable(crossed);
    const lastTurn = turns[turns.length - 1];

    return (
        <div>
            <ActionSplash />
            {completedColor && !againSplash && <CompletionPopup color={completedColor} onClose={onClosePopup} />}
            <ControlBar undo={AgainStore.undo} restart={AgainStore.restart}>
                <div className="again" style={{ backgroundColor }}>
                    <TurnControl onFinish={onFinish} onAbort={onAbort} chosen={chosen} />
                    <Grid
                        gridData={gridData}
                        crossed={crossed}
                        clickable={clickable}
                        onCross={onCross}
                        lastTurn={lastTurn}
                    />
                    <Letters />
                    <RowPoints rowPointsBlocked={rowPointsBlocked} completedRows={completedRows} lastTurn={lastTurn} />
                    <Jokers takenJokers={takenJokers} />
                    <ColorPoints colorPointsBlocked={colorPointsBlocked} completedColors={completedColors} />
                    <Total totalPoints={totalPoints} lastTurn={lastTurn} />
                    <Turns turns={turns} />
                </div>
            </ControlBar>
        </div>
    );
};
