import React from 'react';
import { Icon } from '../../components/icons/icon';
import { __ } from 'first-intl';

type ControlMenuProps = { undo: () => void; restart: () => void };
export const ControlMenu: React.FC<ControlMenuProps> = ({ undo, restart }) => (
    <div className="game-menu">
        <div>
            {undo && (
                <button onClick={undo} className="undo">
                    <Icon.Undo className="reflect" />
                </button>
            )}
            {restart && (
                <button onClick={restart} className="restart">
                    {__('APP.restart')}
                </button>
            )}
        </div>
    </div>
);
